import { UserService } from "./../../services/user.service";
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { Router } from "@angular/router";
import { Component, OnInit } from "@angular/core";
import { NavController, ToastController } from "@ionic/angular";

@Component({
  selector: "app-add-alarm",
  templateUrl: "./add-alarm.page.html",
  styleUrls: ["./add-alarm.page.scss"],
})
export class AddAlarmPage implements OnInit {
  form: FormGroup;
  constructor(
    private navctr: NavController,
    private router: Router,
    private userSrv: UserService,
    private toastCtrl: ToastController
  ) {}

  ngOnInit() {
    this.initialForm();
  }

  async submit() {
    const sleep = this.form.value;
    if (!sleep.horario_dormir) {
      await this.showToast("Informe a hora de dormir");
      return false;
    }
    if (!sleep.horas_sono) {
      await this.showToast("Informe quantas horas de sono");
      return false;
    }
    console.log(sleep);
    this.userSrv.insertUserSleep(sleep).subscribe(
      async () => {
        await this.showToast("Tempo de Sono inserido com sucesso");
        this.router.navigate(["sleep-schedule"]);
      },
      async (error) => {
        await this.showToast(error);
      }
    );
  }

  initialForm() {
    this.form = new FormGroup({
      horario_dormir: new FormControl(null, [Validators.required]),
      horas_sono: new FormControl(null, [Validators.required]),
      vibrar: new FormControl(0),
    });
  }

  onBack() {
    this.navctr.back();
  }
  toogle(event) {
    if (event.detail.checked === true) {
      this.form.value.vibrar = 1;
      console.log(this.form.value.vibrar);
    } else {
      this.form.value.vibrar = 0;
      console.log(this.form.value.vibrar);
    }
  }
  async showToast(message: string) {
    const toast = await this.toastCtrl.create({
      message,
      duration: 500,
    });

    await toast.present();
  }
}
